.blog {
  height: 100%;
}
.blog .blog-cont {
  overflow: auto;
}
.blog .blog-cont .info {
  margin: auto;
}
.blog .blog-cont .info div {
  width: 800px;
  font-size: 24px;
  text-align: center;
  color: #fff;
  background: -webkit-linear-gradient(#fff, #fff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 300;
  margin: 40px auto 60px;
}
.blog .blog-cont .markd {
  width: 800px;
  margin: auto;
  padding: 20px;
  background-color: #fff;
}
.blog .blog-cont .markd a {
  color: #DE463C;
  background: -webkit-linear-gradient(#DE463C, #DE463C);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.blog .blog-cont .markd blockquote {
  height: 30px;
  line-height: 30px;
  padding-left: 10px;
  background-color: #ECF1FC;
  border-left: 5px solid #DE463C;
}
.blog .blog-cont .markd blockquote p {
  color: #7C7C7C;
}
.blog .blog-cont .markd img {
  max-width: 760px;
}
